import Vue from 'vue'
import Router from 'vue-router'
// import Home from 'components/router/home'
// import About from 'components/router/about'
// import Tourism from 'components/router/tourism'
// import Business from 'components/router/business'
// import Education from 'components/router/education'
// import Hotel from 'components/router/hotel'
// import UseCar from 'components/router/usecar'
// import Contact from 'components/router/contact'

Vue.use(Router)

export default new Router({
    linkActiveClass: 'active',
    linkExactActiveClass: 'active',
    mode: 'history',
    routes: [{
            path: '/',
            component: () => import('../components/routers/home.vue')
        },
        {
            path: '/about',
            component: () => import('../components/routers/about.vue'),
            children: [
                {
                    path: '/about/company',
                    component: () => import('../components/about/company.vue'),
                },
                {
                    path: '/about/partners',
                    component: () => import('../components/about/partners.vue'),
                },
                {
                    path: '/about',
                    component: () => import('../components/routers/about.vue'),
                }
            ]
        },
        {
            path: '/tourism',
            component: () => import('../components/routers/tourism.vue')
        },
        {
            path: '/business',
            component: () => import('../components/routers/business.vue')
        },
        {
            path: '/education',
            component: () => import('../components/routers/education.vue')
        },
        {
            path: '/hotel',
            component: () => import('../components/routers/hotel.vue')
        },
        {
            path: '/usecar',
            component: () => import('../components/routers/usecar.vue')
        },
        {
            path: '/contact',
            component: () => import('../components/routers/contact.vue')
        }
    ]
})